<template><undefined>
<h1>DatePicker 日期选择框</h1>
<p>输入或选择日期的控件。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>当用户需要输入一个日期，可以点击标准输入框，弹出日期面板进行选择。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;                        \n    &lt;v-date-picker v-model=\&quot;date1\&quot; clearable @change=\&quot;change\&quot;&gt;&lt;/v-date-picker&gt;\n    &lt;v-date-picker v-model=\&quot;rangeDate1\&quot; range=\&quot;\&quot; clearable @confirm=\&quot;confirm\&quot; @change=\&quot;rangeChange\&quot;&gt;&lt;/v-date-picker&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data:()=&gt; ({\n        date1: '2015-12-06'\n        rangeDate1: ['2015-12-06','2016-12-06']\n    }),\n    watch: {\n        date1(val){\n            console.log('watch:', val)\n        },\n        rangeDate1(val){\n            console.log('watch:', val)\n        }\n    },\n    methods: {\n        confirm() {\n            console.log('confirm')\n        },\n        change(time) {\n            console.log('change:', time)\n        },\n        rangeChange(startTime, endTime) {\n            console.log('change:', startTime, endTime)\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>                        
    <v-date-picker v-model="date1" clearable="" @change="change"></v-date-picker>
    <v-date-picker v-model="rangeDate1" range="" clearable="" @confirm="confirm" @change="rangeChange"></v-date-picker>
</template>

</div>
                  <div slot="description"><h4 id="ji-chu"><a class="header-anchor" href="#ji-chu" aria-hidden="true">¶</a> 基础</h4>
<p>最简单的用法，在浮层中可以选择或者输入日期</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>                        
    <span class="hljs-tag">&lt;<span class="hljs-name">v-date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date1"</span> <span class="hljs-attr">clearable</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"change"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"rangeDate1"</span> <span class="hljs-attr">range</span> <span class="hljs-attr">clearable</span> <span class="hljs-attr">@confirm</span>=<span class="hljs-string">"confirm"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"rangeChange"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-date-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>:<span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
        <span class="hljs-attr">date1</span>: <span class="hljs-string">'2015-12-06'</span>
        rangeDate1: [<span class="hljs-string">'2015-12-06'</span>,<span class="hljs-string">'2016-12-06'</span>]
    }),
    <span class="hljs-attr">watch</span>: {
        date1(val){
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'watch:'</span>, val)
        },
        rangeDate1(val){
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'watch:'</span>, val)
        }
    },
    <span class="hljs-attr">methods</span>: {
        confirm() {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'confirm'</span>)
        },
        change(time) {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'change:'</span>, time)
        },
        rangeChange(startTime, endTime) {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'change:'</span>, startTime, endTime)
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
