<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="datepicker-props"><a class="header-anchor" href="#datepicker-props" aria-hidden="true">¶</a> DatePicker Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>默认日期,当range为true时为数组[开始时间，结束时间]</td>
<td>string/array</td>
<td>-</td>
</tr>
<tr>
<td>size</td>
<td>输入框大小，lg 高度为 32px，sm 为 22px，默认是 28px</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位提示符</td>
<td>string</td>
<td>请选择日期</td>
</tr>
<tr>
<td>position</td>
<td>下拉框的定位方式(absolute、fixed)</td>
<td>string</td>
<td>absolute</td>
</tr>
<tr>
<td>popupContainer</td>
<td>下拉菜单渲染父节点。默认渲染到 body 上，如果你遇到菜单滚动定位问题，试试修改为滚动的区域，并相对其定位。</td>
<td>function</td>
<td>() =&gt; document.body</td>
</tr>
<tr>
<td>range</td>
<td>能否进行范围选择</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>showTime</td>
<td>增加时间选择功能</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>maxRange</td>
<td>选择最大范围限制,以天为单位（只有range为true的时候才起作用）</td>
<td>number</td>
<td>-</td>
</tr>
<tr>
<td>clearable</td>
<td>是否显示清除按钮</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>format</td>
<td>展示的日期格式</td>
<td>string</td>
<td>yyyy-MM-dd</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用</td>
<td>boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="datepicker-events"><a class="header-anchor" href="#datepicker-events" aria-hidden="true">¶</a> DatePicker Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>时间发生变化的时候触发</td>
<td>time or (startTime, endTime)</td>
</tr>
<tr>
<td>confirm</td>
<td>点击确定按钮时触发</td>
<td>-</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
export default {
    data:()=> ({
        date1: '2015-12-06',
        date2: '2015-12-06',
        date3: '2015-12-06',
        date4: '2015-12-06',
        rangeDate1: ['2015-12-06','2016-12-06'],
        rangeDate2: ['2015-12-06','2016-12-06'],
        rangeDate3: ['2015-12-06','2016-12-06'],
        time1: '2015-12-06 23:12',
        time2: '2015-12-06 23:12',
        rangeTime1: ['2015-12-06 23:12','2016-12-06 23:59'],
        rangeTime2: ['2015-12-06 23:12','2016-12-06 23:12']
    }),
    watch: {
        date1(val){
            console.log('watch:', val)
        },
        rangeDate1(val){
            console.log('watch:', val)
        }
    },
    methods: {
        confirm() {
            console.log('confirm')
        },
        change(time) {
            console.log('change:', time)
        },
        rangeChange(startTime, endTime) {
            console.log('change:', startTime, endTime)
        },
        range (start, end){
            const result = [];
            for (let i = start; i < end; i++) {
                result.push(i);
            }
            return result;
        },
        disabledDate(current){
            return current && current.valueOf() < Date.now();
        },
        disabledTime(){
            return [{
                disabledHours: (h) => this.range(0, 24).splice(4, 20).includes(h),
                disabledMinutes: (m) => this.range(30, 60).includes(m)
            }]
        },
        disabledRangeTime(){
            return [{ 
                disabledHours: (h) => this.range(0, 24).splice(4, 20).includes(h),
                disabledMinutes: (m) => this.range(30, 60).includes(m)
            },{
                disabledHours: (h) => this.range(0, 60).splice(20, 4).includes(h),
                disabledMinutes: (m) => this.range(0, 31).includes(m)
            }]
        }
    }
}
</script>


